import LoadMore from '@/components/common/LoadMore.vue';
import {getUserData} from '@/network/getData.js';

import userList from '@/components/content/user-list/user-list.vue';
export default {
	components:{
		LoadMore,
		userList
	},
	data() {
		return {
			followFriendInfo:[],
			//初始化可滚动高度
			scrollH: 800,
			tabIndex: 0,
			tabBar: [{name: ' 互关',num: 0 },{name: '关注',num: 10},{name: '粉丝',num: 20}],
			
		}
	},
	onNavigationBarSearchInputClicked() {
			uni.navigateTo({
				url: '../search/search?type=user',
			});
	},
	onNavigationBarButtonTap() {
			uni.navigateBack({
				delta: 1
			});
	},
	onLoad() {
		uni.getSystemInfo({
			success: res => {
				this.scrollH = res.windowHeight - uni.upx2px(100);
			}
		});
		//获取数据
		this.followFriendInfo = getUserData(this.tabBar.length)
	},
	
	methods: {
		changeTab(index){
			this.tabIndex = index;
		},
		onChangeTab(e){
			
			this.changeTab(e.detail.current)
		},
		//上拉加载更多功能实现
		loadmore(index) {
			//拿到当前列表页
			let item = this.newsList[index];
			//判断是否处于可加载状态
			if (item.loadmore !== '上拉加载更多') return;
			// 修改当前列表页加载状态为加载中
			item.loadmore = '加载中';
			//模拟数据请求
			setTimeout(() => {
				//加载数据
				item.list = [...item.list, ...item.list];
				//回复加载状态
				item.loadmore = '上拉加载更多';
			}, 1000);
		},
		
	}
}
